  max-width: 600px;
  border-radius: 0px; }
  .tiny > .modal {
    max-width: 300px; }
  .small > .modal {
    max-width: 500px; }
  .large > .modal {
    max-width: 800px; }
  .dialog > .modal {
    height: auto; }
  .collapse > .modal {
    padding: 0; }

.modal-overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: none;
  background-color: rgba(51, 51, 51, 0.7);
  align-items: center;
  justify-content: center;
  -webkit-transform: translate3d(0, 0, 0); }
  .modal-overlay.is-active {
    display: flex; }

